import Chip from '@mui/material/Chip';
import './Jobs.scss';

function Jobs() {

    const allJobs = [
        {
            company: 'MyRent',
            aboutCompany: 'MyRent is a property management software company that provides tools for landlords and property managers to manage their rental properties efficiently.',
            website: 'https://my-rents.com',
            city: 'Zagreb',
            country: 'Croatia',
            locationType: 'Hybrid',
            positions: ['ASP.NET Developer', 'Content Writer'],
            positionsDescription: [
                'Developed engaging web blogs and articles using WordPress, enhancing the company’s online presence.',
                'Utilized GitHub for version control and effective collaboration with colleagues.',
                'Edited and maintained the CMS in ASP.NET, ensuring seamless content updates and user experience.',
                'Used Visual Studio for programming tasks, improving efficiency in development processes.',
                'Assisted clients by clarifying different sections of the CMS.',
                'Used MS Office.'
            ],
            skills: [
                'Teamwork', 'Collaboration', 'Wrting blogs', 'CMS'
            ]
        }
    ];

    return (
        <div className='jobs-component'>
            <h1>Professional experience</h1>

            <div className='jobs-list'>
                {
                    allJobs.map((job: any, index: number) =>

                        <div className="one-job-card" key={index}>
                            <h2>{job.company}</h2>
                            <p>{job.aboutCompany}</p>
                            <p>
                                <strong>
                                    <a href={job.website}>{job.website}</a>
                                </strong>
                            </p>
                            <h3>Positions</h3>
                            {
                                job.positions.map((position: string, posIndex: number) => <Chip key={posIndex} label={position} className='position-chip' />)
                            }
                            {
                                job.positionsDescription.map((description: string, descIndex: number) => <p key={descIndex}>👉{description}</p>)
                            }
                            <h3>Skills</h3>
                            {
                                job.skills.map((skill: string, skillIndex: number) => <Chip key={skillIndex} label={skill} className='skill-chip' />)
                            }
                        </div>
                    )
                }</div>
        </div>
    );
}

export default Jobs;