import React from "react";
import { Routes, Route, Outlet, Navigate } from "react-router-dom";
import Projects from "./WORKSPACE/Projects/Projects";
import AboutMe from "./WORKSPACE/AboutMe/AboutMe";
import Web from "./WORKSPACE/Projects/Web/Web";
import Desktop from "./WORKSPACE/Projects/Desktop/Desktop";
import Games from "./WORKSPACE/Projects/Games/Games";
import Arduino from "./WORKSPACE/Projects/Arduino/Arduino";
import Android from "./WORKSPACE/Projects/Android/Android";
import Business from "./WORKSPACE/Projects/Business/Business";
import Jobs from "./WORKSPACE/Jobs/Jobs";
import Marketing from "./WORKSPACE/Projects/Marketing/Marketing";

const AppRoutes = () => {
    return (
        <Routes>
            <Route path="/" element={<AboutMe />} />
            <Route path="projects" element={<Projects />}>
                <Route path="web" element={<Web />} />
                <Route path="desktop" element={<Desktop />} />
                <Route path="android" element={<Android />} />
                <Route path="games" element={<Games />} />
                <Route path="arduino" element={<Arduino />} />
                <Route path="business" element={<Business />} />
                <Route path="marketing" element={<Marketing />} />
            </Route>
            <Route path="jobs" element={<Jobs />} />
            <Route path="*" element={<Navigate to='/' replace />} />
        </Routes>
    );
};

export default AppRoutes;