import './index.scss';
import ReactDOM from 'react-dom/client';
import Sidebar from './SIDEBAR/Sidebar';
import { BrowserRouter } from 'react-router-dom';
import AppRoutes from './routes';
import { useState } from 'react';
import { OnlyOnceContext } from './WORKSPACE/Shared/Contexts';

const root = ReactDOM.createRoot(document.getElementById('root') as HTMLElement);
root.render(<Index />);

function Index(props: any) {
  window.screen.orientation.addEventListener('change', () => { document.body.style.transform = 'scale(1)'; });
  window.onresize = () => { /*console.log(`Window is resizing -> ${Date.now()}`) */ }

  const [onlyOnce, setOnlyOnce] = useState(true);
  return (
    <>
      <OnlyOnceContext.Provider value={{ onlyOnce, setOnlyOnce }}>
        <BrowserRouter>
          <header>
            <Sidebar />
          </header>
          <main>
            <AppRoutes />
          </main>
        </BrowserRouter>
      </OnlyOnceContext.Provider>
    </>
  );
}
export default Index;


