import '../AboutMe/AboutMe.scss'
import Chip from "@mui/material/Chip";
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';

function AboutMe() {

    const interests = ['Technology', 'Software development', 'Sports', 'Nature', 'Web', 'Mobile', 'Desktop', 'MS Office', 'Music', 'Movies', 'Games', 'Learning new things', 'Running', 'Cycling'];
    const skills = ['Teamwork', 'Critical thinking', 'Communication', 'Adaptability', 'Perseverance', 'Technical skills', 'Creativity', 'Time management', 'Attention to detail'];
    const academicTitles = ['bacc. ing. comp.', 'M.Eng.IT'];
    const spokenLanguages = ['Croatian', 'English', 'German'];

    return (
        <div id='about-me-component'>

            <h1>About Me</h1>
            <div className="about-me-content">
                <p>Hi, I'm a software developer. I have experience in various programming languages and frameworks. In my free time, I love exploring new technologies and learning about the latest trends in software development.</p>
                <div>
                    <h3>Interests</h3>{
                        interests.map((interest: any, index: number) => <Chip key={index} label={interest} className='interest-chip' />)
                    }
                    <h3>Skills</h3>{
                        skills.map((skill: any, index: number) => <Chip key={index} label={skill} className='skill-chip' />)
                    }
                    <h3>Languages</h3>{
                        spokenLanguages.map((language: any, index: number) => <Chip key={index} label={language} className='language-chip' />)
                    }
                    <h3>Education</h3>{
                        academicTitles.map((title: any, index: number) => <Chip key={index} label={title} className='academic-title-chip' />)
                    }
                    <h3>Contact</h3>
                    <div className="social-links">
                        <a href="https://github.com/LukaSpoljar" >
                            <GitHubIcon className="social-icon-link" fontSize="large" />
                        </a>
                        <a href="https://www.linkedin.com/in/lukaspoljar">
                            <LinkedInIcon className="social-icon-link" fontSize="large" />
                        </a>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default AboutMe;