import './Sidebar.scss';
import { useEffect } from "react";
import Button from '@mui/material/Button';
import { useNavigate } from 'react-router-dom';
import InfoIcon from '@mui/icons-material/Info';
import AssignmentIcon from '@mui/icons-material/Assignment';
import WorkHistoryIcon from '@mui/icons-material/WorkHistory';
import { AllProjects } from "..";


function Sidebar(props: any) {

    const getTotalNumOfProjects = () => {
        let startCounter: number = 0;
        for (const key in AllProjects)
            startCounter += AllProjects[key as keyof typeof AllProjects].projects.length;
        return startCounter;
    }
    const navigate = useNavigate();

    const totalNumOfProjects = getTotalNumOfProjects();

    useEffect(() => {
        // Initialization logic
        // navigate('/projects')
        return () => {
            // Cleanup logic
            //console.log('Component destroyed (unmounted)');
        };
    }, []);

    return (
        <div id='sidebar-wrapper'>

            <div id='avatar-wrapper'>
                <img id='profile-image' src="/Images/profile.png" alt="Profile" />
            </div>

            <div id='under-avatar-wrapper'>
                <h1>Ivan Luka Špoljar</h1>
                <nav>
                    <Button variant="contained" onClick={() => navigate('/')} endIcon={<InfoIcon />}>
                        <strong>About Me</strong>
                    </Button>

                    <Button variant="contained" onClick={() => navigate('/jobs')} endIcon={<WorkHistoryIcon />}>
                        <strong>Jobs</strong>
                    </Button>

                    <Button variant="contained" onClick={() => navigate('/projects')} endIcon={<AssignmentIcon />}>
                        <strong>Projects</strong>
                    </Button>
                </nav>

                <h3><strong>Projects: {totalNumOfProjects}</strong></h3>

            </div>
        </div>
    );
}

export default Sidebar;