import './Content.scss';
import { Chip } from '@mui/material';

function Content(props: any) {

    const componentTitle = props?.data.categoryTitle || props?.data.componentTitle;
    const projects = props?.data.projects;

    return (
        <div id='content-component'>
            <h2>{componentTitle}</h2>
            <div id='contnet-with-cards'>
                {
                    projects.map((item: any, index: number) => {
                        return (
                            <div key={item?.id} className='card-item-component' >

                                <a href={item?.gitHubLink}>
                                    <h3>{item?.title}</h3>
                                </a>

                                <div className='card-item-main-content'>
                                    <div className='card-item-description-wrapper'>
                                        <article>
                                            {item?.description}
                                        </article>

                                        <div className='chips'>
                                            {item?.technologies.map((tech: any, indexSecond: number) => <Chip className='one-chip' key={indexSecond} label={tech} />)}
                                        </div>
                                    </div>

                                    {item?.coverImageLink && item?.id != 'web_10' &&
                                        <div className='project-cover-image-wrapper'>
                                            <img src={item?.coverImageLink} />
                                        </div>
                                    }

                                    {
                                        item?.coverVideoLink &&
                                        <div className='project-cover-video-wrapper'>
                                            <iframe
                                                src={item.coverVideoLink}
                                                title="YouTube video player"
                                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                allowFullScreen>
                                            </iframe>
                                        </div>
                                    }
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        </div>
    )
}

export default Content